<template>
  <div  class="container flex-center-column city-card" v-if="isLoaded" >
      <div >
        <h1 class="">{{ city.city }}</h1>
        <p class="">
          {{
            new Date(weatherData.currentTime).toLocaleDateString(
              "en-us",
              {
                weekday: "short",
                day: "2-digit",
                month: "long",
              }
            )
          }}
          {{
            new Date(weatherData.currentTime).toLocaleTimeString(
              "en-us",
              {
                timeStyle: "short",
              }
            )
          }}
        </p>
        <p class="">
          {{ Math.round(weatherData.current.temp/33,8) }}&deg;
        </p>
        <p>
          Feels like
          {{ Math.round(weatherData.current.feels_like/33,8) }} &deg;
        </p>
        <p class="">
          {{ weatherData.current.weather[0].description }}
        </p>
        <img
          class=""
          :src="
            `http://openweathermap.org/img/wn/${weatherData.current.weather[0].icon}@2x.png`
          "
          alt=""
        />
       
      </div>
      <hr class="" />

    <!-- Hourly Weather -->
    <div class="">
      <div class="">
        <h2 class=" title">Hourly Weather</h2>
        <div class="hourly">
          <div
            v-for="hourData in weatherData.hourly"
            :key="hourData.dt"
            class=""
          >
            <p class="">
              {{
                new Date(
                  hourData.currentTime
                ).toLocaleTimeString("en-us", {
                  hour: "numeric",
                })
              }}
            </p>
            <img
              class=""
              :src="
                `http://openweathermap.org/img/wn/${hourData.weather[0].icon}@2x.png`
              "
              alt=""
            />
            <p class="">
              {{ Math.round(hourData.temp/33,8) }}&deg;
            </p>
            <div :style="{height:Math.round(hourData.temp)/10 + 'px' }" class="white"></div>
          </div>
        </div>
      </div>
    </div>

    <hr class="" />

    <!-- Weekly Weather -->
    <div class="">
      <h2 class="title">7 Day Forecast</h2>
      <div class="hourly">
        <div
          v-for="day in weatherData.daily"
          :key="day.dt"
          class="dayli"
        >
          <p class="flex-1">
            {{
              new Date(day.dt * 1000).toLocaleDateString(
                "en-us",
                {
                  weekday: "long",
                }
              )
            }}
          </p>
          <img
            class=""
            :src="
              `http://openweathermap.org/img/wn/${day.weather[0].icon}@2x.png`
            "
            alt=""
          />
          <div class="">
            <p>H: {{ Math.round(day.temp.max) }}</p>
            <p>L: {{ Math.round(day.temp.min) }}</p>
          </div>
        </div>
      </div>
    </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "WeatherBlock",
  props: {
    city: {
      type: Object,
      default: {},
    },
  },
  data() {
      return {
        weatherData: {},
        isLoaded: false
      };
    },
    methods: {

      async getWeather() {
        try {
          const weather = await axios.get(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.city.geo.lat}&lon=${this.city.geo.lng}&exclude={part}&appid=7efa332cf48aeb9d2d391a51027f1a71`);
          // cal current date & time
    const localOffset = new Date().getTimezoneOffset() * 60000;
    const utc = weather.data.current.dt * 1000 + localOffset;
    weather.data.currentTime =
      utc + 1000 * weather.data.timezone_offset;

    // cal hourly weather offset
    weather.data.hourly.forEach((hour) => {
      const utc = hour.dt * 1000 + localOffset;
      hour.currentTime =
        utc + 1000 * weather.data.timezone_offset;
    });
        this.weatherData = weather.data
        this.isLoaded = true;
        } catch (error) {
          alert('ERror')
        }
      }
    },
  mounted () {
   this.getWeather()
  },
};
</script>

<style scoped>
.white {
  background-color: white;
  width: 10px;
}
.hourly {
  display: flex;
  gap: 10px;
  overflow-x: scroll;
  overflow-y: hidden;
  align-items: stretch;
  max-width: 1200px;
}
  /* полоса прокрутки (скроллбар) */
  .hourly::-webkit-scrollbar {
    width: 8px; /* ширина для вертикального скролла */
    height: 8px; /* высота для горизонтального скролла */
    background-color: transparent;
}

/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
    background-color: #e7f5dc;
    border-radius: 9em;
    box-shadow: inset 1px 1px 10px #f3faf7;
}

::-webkit-scrollbar-thumb:hover {
  background-color: transparent;
}

/* Стрелки */

::-webkit-scrollbar-button:vertical:start:decrement {
    background-color: transparent;
}

::-webkit-scrollbar-button:vertical:end:increment {
  background-color: transparent;
}

::-webkit-scrollbar-button:horizontal:start:decrement {
  background-color: transparent;
}

::-webkit-scrollbar-button:horizontal:end:increment {
  background-color: transparent;
}

.dayli {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.flex {
  display: flex;
}
.title {
  text-align: center;
  margin-bottom: 10px;
}
.city-card {
  border:solid 1px #e7f5dc;
  margin: 10px;
  padding: 20px;
  border-radius: 5px;
  overflow:hidden;
}
</style>
